body{
  -webkit-print-color-adjust:exact;
}
html, body {
  height: 99%;
  page-break-after: avoid;
  page-break-before: avoid;
}
#export-options {
  display: none;
}
.export-table {
  margin: 0;
  padding: 0;
}
h3 {
  text-align: center;
}
.wp-list-table.export-table {
  thead,
  tfoot {
    tr {
      background: #333 !important;
      th {
        color: #FFF !important;
      }
    }
  }
}
